:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.tab {
	position: relative;
	border: 1px solid black;
	background: #FFF;
}

.close {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	right: 5px;
	cursor: pointer;
	font-size: 0;
	border: none;
	background: none;
	padding: 1px 3px;
}

.hidden {
	display: none;
}

.close:after {
	content: 'âœ•';
	display: block;
	font-size: 14px;
}

.closeable {}

.activeTabButton {
	background: #FFF;
	border-color: black;
	border-bottom-color: #FFF;
	z-index: calc(0 + 1);
	z-index: calc(var(--zindex-base) + 1);
}

.root, .root * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.tabButton {
	border: 1px solid transparent;
	border-top: 2px solid transparent;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	width: auto;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	cursor: pointer;
	position: relative;
	top: 1px;
}

.tabButton:focus:not(.disabledTabButton) {
	font-weight: bold;
}

.disabledTabButton {
	font-style: italic;
	cursor: default;
	color: #bbb;
	color: var(--disabled-color);
}

.alignLeft .tabs {
	display: inline-block;
	vertical-align: top;
}

.alignLeft .tabButtons {
	display: inline-block;
	vertical-align: top;
}

.alignLeft .tabButton {
	border-top: 1px solid transparent;
	border-left: 2px solid transparent;
	border-bottom: 1px solid transparent;
	border-right: 1px solid transparent;
	display: block;
	top: 0;
	left: 1px;
}

.alignLeft .activeTabButton {
	border-top-color: #34495e;
	border-top-color: var(--selected-background);
	border-left-color: #34495e;
	border-left-color: var(--selected-background);
	border-bottom-color: #34495e;
	border-bottom-color: var(--selected-background);
	border-right: 1px solid #FFF;
}

.alignBottom .tabButton {
	border-top: none;
	border-bottom: 2px solid transparent;
	top: -1px;
}

.alignBottom .activeTabButton {
	border-bottom-color: #34495e;
	border-bottom-color: var(--selected-background);
}

.alignRight .tabs {
	display: inline-block;
	vertical-align: top;
}

.alignRight .tabButtons {
	display: inline-block;
	vertical-align: top;
}

.alignRight .tabButton {
	border-top: 1px solid transparent;
	border-right: 2px solid transparent;
	border-bottom: 1px solid transparent;
	border-left: none;
	display: block;
	top: 0;
	left: -1px;
}

.alignRight .activeTabButton {
	border-top-color: #34495e;
	border-top-color: var(--selected-background);
	border-right-color: #34495e;
	border-right-color: var(--selected-background);
	border-bottom-color: #34495e;
	border-bottom-color: var(--selected-background);
}



